import React from 'react'
import { useInView } from 'react-intersection-observer';
import '../styles/dropship.css'

export default function Dropship() {
    const [proporciona, proporcionaView] = useInView();
    return (
        <div className='wrapper-dropship'>
            <div className="container-xl">
                <div className="row my-4 dropship align-items-center justify-content-center">
                    <div className="col-6 col-md-4">
                        <img className='img-fluid' src="/dropship/images/dropship-banner.png" alt="" />
                    </div>
                    <div className="col-md-8">
                        <div ref={proporciona} className={proporcionaView ? 'show' : 'from-left'}>
                            <h1>DROPSHIPPING PROPORCIONA:</h1>
                        </div>
                        <p>Liberdade Financeira:<br />Voc ser o prprio regente da sua vida financeira, e vai conseguir conquistar tudo que voc sempre desejou.</p>
                        <p>Liberdade de tempo:<br />Voc vai poder fazer o seu prprio horrio, pois a sua maquina de vendas sempre estar trabalhando para voc.</p>
                        <p>Liberdade geogrfica:<br />Voc vai poder trabalhar onde quiser, basta ter um celular ou computador com conexo na internet.</p>
                    </div>
                </div>
                <div className="row gx-5 mt-5 obrigatorio">
                    <h1>O CURSO  OBRIGATRIO PARA VOC QUE QUER:</h1>
                    <div className="col-md-6 col-lg-3 my-3 my-lg-0">
                        <div className='obrigatorio-card'>
                            <img className='img-fluid' src="/dropship/images/fonte-renda.png" alt="" />
                            <h3>UMA NOVA FONTE DE RENDA</h3>
                        </div>
                    </div>
                    <div className="col-md-6 col-lg-3 my-3 my-lg-0">
                        <div className='obrigatorio-card'>
                            <img className='img-fluid' src="/dropship/images/sem-investir.png" alt="" />
                            <h3>TRABALHAR SEM INVESTIR EM ESTOQUE</h3>
                        </div>
                    </div>
                    <div className="col-md-6 col-lg-3 my-3 my-lg-0">
                        <div className='obrigatorio-card'>
                            <img className='img-fluid' src="/dropship/images/proprio-chefe.png" alt="" />
                            <h3>SER SEU PRPRIO CHEFE</h3>
                        </div>
                    </div>
                    <div className="col-md-6 col-lg-3 my-3 my-lg-0">
                        <div className='obrigatorio-card'>
                            <img className='img-fluid' src="/dropship/images/alavancar.png" alt="" />
                            <h3>ALAVANCAR SEUS NEGCIOS</h3>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
